html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
  line-height: 1.2;
  background-color: #f5f3f3;
}

[class*=__titulo] {
  margin: 2rem 0;
}

h1 {
  font-size: 3.6rem;
  font-weight: 700;
}

h2 {
  font-size: 2.8rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 900;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

[class*=__contenedor] {
  width: 85%;
  max-width: 96rem;
  margin: 0 auto;
}

.header {
  background-color: #FFF;
  padding: 2rem 0 0;
}
.header__titulo {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  display: block;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
@media (min-width: 768px) {
  .menu {
    flex-direction: row;
    justify-content: space-between;
    padding: 0 11rem 0 6rem;
    align-items: flex-start;
  }
}
.menu__img {
  margin: 0 auto 0.5rem;
}
@media (min-width: 768px) {
  .menu__img {
    margin-bottom: 0;
  }
}

.cart__cont:focus-within .cart {
  display: block;
}

.cart {
  display: none;
  background-color: #FFF;
  padding: 1rem;
  position: absolute;
  bottom: -28rem;
  height: 25rem;
  left: 0;
  right: 0;
}
@media (min-width: 768px) {
  .cart {
    width: 60%;
    right: 0;
    bottom: -43rem;
    left: auto;
    height: 40rem;
  }
}
.cart a {
  background-color: #f5f3f3;
  font-size: 1.3rem;
  display: block;
  width: 100%;
  text-align: center;
  color: #000;
  text-decoration: none;
  border: none;
  padding: 1rem 4rem;
  margin: 1rem 0;
  text-transform: uppercase;
  font-weight: 700;
  transition: 0.3s background-color;
}
.cart a:hover {
  cursor: pointer;
  background-color: rgb(220.5, 218.7, 218.7);
}
.cart a {
  border: 1px solid #aaa;
  border-radius: 0.5rem;
}
.cart table,
.cart thead,
.cart tbody {
  display: block;
}
.cart table tr,
.cart thead tr,
.cart tbody tr {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
}
.cart table {
  margin-bottom: 3rem;
  border-bottom: 1px solid #aaa;
}

.avif .hero {
  background-image: url("../img/hero.avif");
}

.noavif.webp .hero {
  background-image: url("../img/hero.webp");
}

.noavif.nowebp .hero {
  background-image: url("../img/hero.jpg");
}

.hero {
  height: 35rem;
  margin-top: 3rem;
  background-size: cover;
  background-position: left top;
  background-repeat: no-repeat;
  padding: 3rem;
}
.hero__contenedor {
  max-width: 96rem;
}
.hero__titulo {
  color: #FFF;
  font-weight: 500;
  font-size: 3rem;
  margin-top: 5rem;
}
.hero__p {
  color: #FFF;
  font-weight: 400;
  margin-bottom: 3rem;
}

.form .busqueda {
  background-color: #FFF;
  display: flex;
  height: 5rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .form .busqueda {
    width: 48%;
  }
}
.form .busqueda__input {
  flex: 1;
  height: 100%;
  border: none;
  border-radius: 0.5rem 0 0 0.5rem;
  padding: 0 1rem;
  outline: none;
}
.form .busqueda__submit {
  outline: none;
  height: 100%;
  flex-basis: 5rem;
  background-image: url("../img/lupa.png");
  background-color: transparent;
  background-repeat: no-repeat;
  background-position-x: 100%;
  background-position: 50% center;
  border: none;
}

.barra {
  background-image: linear-gradient(to right, #7b2051, #e44e52);
  padding: 2rem 0;
}
.barra__contenedor {
  width: 90%;
  max-width: 96rem;
}
@media (min-width: 768px) {
  .barra__contenedor {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .barra__contenedor {
    gap: 3rem;
  }
}
.barra .caracteristica {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .barra .caracteristica {
    margin-bottom: 0;
  }
}
.barra .caracteristica__img {
  flex-basis: 10%;
  object-fit: scale-down;
  height: 3.5rem;
  width: 100%;
}
@media (min-width: 768px) {
  .barra .caracteristica__img {
    flex-basis: 15%;
  }
}
.barra .caracteristica__p {
  flex: 1;
  text-align: center;
  color: #FFF;
  margin: 0;
  line-height: 1.5;
}

.cursos {
  padding-bottom: 5rem;
  border-bottom: 1px solid #aaa;
}
.cursos__title {
  text-align: center;
  font-weight: 500;
  font-size: 4rem;
  text-transform: capitalize;
}
@media (min-width: 768px) {
  .cursos__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}

.card {
  background-color: #FFF;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .card {
    margin-bottom: 0;
  }
}
.card__img {
  width: 100%;
}
.card__info {
  padding: 2rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .card__info {
    text-align: left;
  }
}
.card__title {
  font-size: 1.5rem;
  font-weight: 800;
  margin: 0.5rem 0;
}
.card__instructor {
  margin: 0 0 0.5rem;
}
.card__calificacion {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .card__calificacion {
    margin-left: 0;
  }
}
.card__btn {
  background-color: #33C3F0;
  font-size: 1.3rem;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFF;
  text-decoration: none;
  border: none;
  padding: 1rem 4rem;
  margin: 1rem 0;
  text-transform: uppercase;
  font-weight: 700;
  transition: 0.3s background-color;
}
.card__btn:hover {
  cursor: pointer;
  background-color: rgb(45.9, 175.5, 216);
}

.descuento {
  display: flex;
  margin: 1rem 0 0.5rem;
}
@media (min-width: 768px) {
  .descuento {
    justify-content: space-between;
  }
}
.descuento__p {
  font-size: 2.2rem;
  font-weight: 700;
  margin: 1rem 0;
  margin-left: auto;
}
@media (min-width: 768px) {
  .descuento__p {
    margin-left: 0;
  }
}
.descuento__p--dashed {
  text-decoration: line-through;
  font-weight: 400;
  font-size: 1.8rem;
}

footer {
  padding: 5rem 0 0;
}
@media (min-width: 768px) {
  footer {
    display: flex;
    gap: 5rem;
  }
}

.menu-footer__element,
.nosotros__element {
  display: block;
  color: #33C3F0;
  margin-bottom: 1.5rem;
  text-transform: capitalize;
  font-size: 1.5rem;
}